<template>
	<div class="prouduct-detail">
		<span class="lt title-text">产品系列</span>
		<span class="lt entitle">Product series</span>
		<div class="product-link">
			<div @click="jumprouter(item)" class="mix-content" v-for="(item, index) in prouductDetail">
				<img :class="'a' + item" :src="'static/image/prouduct-img/' + item + '.jpg'"/>
				<span>{{index}}</span>
			</div>
		</div>
		<span class="lt title-text">供应商</span>
		<span class="lt entitle">Supplier</span>
		<div class="friend-link">
			<div class="friend-link-wrapper">
				<div class="friend-logo" v-for="(item, index) in logoLink">
					<img :src="'static/image/bright-img/friend-logo/' + item + '.jpg'"/>
					<span class="explain-word">{{index}}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
export default {
	data() {
		return {
			logoLink: {STRUKTOL:2,POLYRAM:11,OKABEST:7,ROQUETTE:12,BYK:3,FRX:6,DC:9,VELOX:13,APV:14,BOSTIK:10,LUCOBIT:5,BUDENHEIM:1,REAGENS:15,VEMAB:8,TOLSA:4,}
		}
	},
	methods: {
		jumprouter(index) {
			let num = 'prouductCenter/page' + index
			let routid = 'arch' + index
			console.log(this.$route)
			this.$router.push({path: num})
			localStorage.setItem('id', routid)
		}
	},
	computed: {
		prouductDetail() {
			return this.$t('prouductDetail')
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"

	.product-link
		width: 800px
		margin: 20px auto 0
		display: flex
		flex-wrap: wrap
		justify-content: flex-start
		@media screen and (max-width:550px)
			width: 75%
			justify-content: space-between
		.mix-content
			display: flex
			flex-direction: column
			align-items: center
			margin: 30px
			@media screen and (max-width:550px)
				margin: 5px
			img
				display: block
				@media screen and (max-width:550px)
					width: 120px
			span
				font-size: $font-web-small
	.lt
		display: block
		text-align: center
	.title-text
		font-size: $font-web-medium
		color: $color-theme
	.entitle
		font-size: $font-web-small-l
	.friend-link
		position: relative
		margin: 30px 0 
		@media screen and (max-width:550px)
			margin-bottom: 0
		.all-product
			width: 150px
			display: block
			margin: 0 auto
			text-align: center
			line-height: 30px
			height: 30px
			font-size: $font-web-small
			background: $color-theme
			color: $color-text
			cursor: pointer
		.friend-link-wrapper
			width: 80%
			margin: 30px auto 50px
			display: flex
			flex-wrap: wrap
			justify-content: space-between
			.friend-logo
				width: 20%
				padding: 20px
				cursor: pointer
				@media screen and (max-width:850px)
					width: 30%
				img
					width: 75%
					padding-left: 25%
					@media screen and (max-width:550px)
						width: 100%
						padding: 0
				.explain-word
					display: block
					text-align: center
</style>



