<template>
	<div class="small-search">
		<div class="search-title1">
			<span class="search-pro">{{searchpro[0]}}</span>
			<div class="wrapper">
				<input class="inner"></input>
				<SearchSvg></SearchSvg>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
	import SearchSvg from 'common/only-svg/search-svg'

	export default {
		name:"smallSearch",
		data() {
			return {
				searchpro: this.$t('searchpro')
			}
		},
		components: {
			SearchSvg
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"

		.search-title1
			position: relative
			margin-left: 140px
			margin-top: -16px
			width: 10%
			background: $color-theme
			height: 30px
			.search-pro
				font-size: $font-web-small
				color: $color-text
				text-align: center
				display: block
				line-height: 22px
			.wrapper
				display: flex
				width: 240px
				.inner
					height: 22px
					width: 230px
					background: $color-text-m
				.search-svg
					display: inline-block
					margin-left: -22px
</style>



