<template>
	<div class="tab">
		<img :src="'static/image/' + myimg + '.jpg'" width="100%"/>
	</div>
</template>

<script type="text/ecmascript-6">
	export default {
		props: {
			myimg: {
				type: String,
				default: ''
			}
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"
	@import "~common/stylus/minxin"

	.tab
		width: 100%
		font-size: 0
</style>


