<template>
  <div id="app">
    <div class="main-wrapper">
       <m-header v-show="isShow" class="fixed-line"></m-header>
       <tab v-show="isShow"></tab>
       <blue-tab v-show="isShow"></blue-tab>
       <chang-title @clickIcon="alclickIcon" v-show="!isShow" class="showtitle"></chang-title>
       <top-left @leftTopWhite="leftTopWhite" ref="chang" class="top-abs"></top-left>
       <router-view></router-view>
       <bottom-adr></bottom-adr>
     </div>
  </div>
</template>

<script>
  import MHeader from 'components/m-header/m-header'
  import Tab from 'components/tab/tab'
  import blueTab from 'components/blue-tab/blue-tab'
  import bottomAdr from 'components/bottom-adr/bottom-adr'
  import changTitle from 'common/chang-title/chang-title'
  import topLeft from 'common/top-left/top-left'

  let flag = true

  export default {
    data() {
      return {
        isShow: true,
        fullWidth: document.body.clientWidth
      }
    },
    created() {
      this.fullWidth > 550 ? this.isShow = true : this.isShow = !true
      window.addEventListener('resize', this.handleResize)
    },
    beforeDestroy() {
      window.removeEventListener('resize', this.handleResize)
    },
    methods: {
      handleResize() {
        this.fullWidth = document.body.clientWidth
      },
      moveMenu() {
        console.log('move')
      },
      alclickIcon() {
        let topLeft = this.$refs.chang
        if(flag) {
          topLeft.$el.style.setProperty("left", 0 )
          flag = !flag
        } else {
          topLeft.$el.style.setProperty("left", '-100%' )
          flag = !flag
        }
      },
      leftTopWhite() {
        console.log('clickicon')
        this.alclickIcon()
      }
    },
    watch: {
      fullWidth(wid) {
        wid > 550 ? this.isShow = true : this.isShow = !true
      }
    },
    components: {
      MHeader,
      Tab,
      blueTab,
      bottomAdr,
      changTitle,
      topLeft
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  #app
    position: fixed
    top: 0
    width: 100%
    bottom: 0
    .main-wrapper
      height: 100%
      overflow: scroll
      overflow-x: hidden
      .fixed-line
        z-index: 100
</style>



