<template>
	<div class="second-company">
		<span class="cntitle">{{mainWord2.chTitle}}</span>
		<span class="circle">.</span>
		<span class="entitle">{{mainWord2.enTitle}}</span>
		<div class="introduct-im">
			<div class="imgwrapper" v-for="item in mainImg">
				<img :src="'static/image/serive-img/' + item + '.jpg'"/>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
export default {
	data() {
		return {
			mainImg: [4,5]
		}
	},
	computed: {
		mainWord2() {
			return this.$t('mainWord2')
		}
	}
}
</script>

<style  lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"

	.cntitle
		font-size: $font-web-medium
		text-align: center
		@media screen and (max-width:550px)
			font-size: $font-web-small-l
	.entitle
		font-size: $font-web-small
	.circle
		font-size: $font-web-large
	.introduct-im
		border-top: 1px solid $color-theme
		padding-top: 60px
		display: flex
		@media screen and (max-width:550px)
			display: block
			padding-top: 20px
		.imgwrapper
			flex: 1
			padding-right: 30px
			@media screen and (max-width:550px)
				padding-bottom: 20px
			img
				width: 100%
		.imgwrapper:nth-child(2)
			padding-right: 0px
</style>



